.GridItem {
  cursor: pointer;
  overflow: hidden;
  width: 350px;
  height: 350px;
  position: relative;
  margin-bottom: 1%;
}

.GridItemContent {
  position: absolute;
}

.GridImage {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
}

.GridItemTextContainer {
  opacity: 0;
  font-size: 2.5rem;
  text-align: center;
  bottom: 0+2.5rem;
  left: 0;
  position: absolute;
}

.GridItem:hover {
  .GridItemTextContainer {
    opacity: 1;
  }

  .GridImage {
    opacity: 0.5;
  }

  .GridItemText {
    opacity: 1;
  }
}
